@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;700&display=swap');

.Contact {
    background-color: rgba(0,0,0,0.5);
    height: 100vh;
}

.Content {
    background-color: rgba(0,0,0,0.5);
    height: 220px;
    width: 1000px;
    margin: auto;
    color: gainsboro;
    padding: 20px;
    position: relative;
    top: 20%;
    font-family: 'Exo 2', sans-serif;
    transform: translateX(1200px);
    animation: ex 0.75s ease-in;
    animation-fill-mode: forwards;
}

@keyframes ex {
    from {transform: translateX(1200px)}
    to {transform: translateX(0px)}
}

.Content h1 {
    margin: 0 0 20px 10px;
    padding: 0;
}

.Content h1 span {
    color: #b3eda5;
}

.Bar {
    width: 100%;
    height: 1px;
    background-color: #b3eda5;
}

.Content2 {
    margin-top: 20px;
}

.Content2 p {
    padding: 0 10px;
}

.ContactMethods {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
}

@media (min-width: 1600px) {
    .Content {
        width: 1400px;
        height: 325px;
    }
    .Content h1 {
        font-size: 275%;
        margin: 10px 0 30px 20px;
    }
    .Content2 {
        font-size: 145%;
        margin-top: 30px;
    }

    .ContactMethods {
        margin-top: 50px;
    }
}
@media (max-width: 450px) {
    .Content {
        top: 10%;
        height: 370px;
        width: 280px;
    }
    .Content h1 {
        font-size: 24px;
    }
    .Content2 p {
        font-size: 16px;
    }
    .ContactMethods {
        flex-direction: column;
        font-size: 14px;
    }
}